<template>
  <div class="picitem">
    <router-link tag="div" :to="`/songitem/${item.id}`">
    <img :src="item.picUrl">
    <p>{{item.name}}</p>
    <i>{{item.playCount|formatNum}}</i>
    </router-link>
  </div>
</template>
<script>
export default {
  props:{
    item:Object
  },
  filters:{
    formatNum(value){
      return (parseInt(value)/10000).toFixed(2)+"万"
    }
  }
}
</script>
<style lang="less" scoped>
img{
  display: block;
  border: none;
  height: auto;
  max-width: 100%;
}
p{
  font-size: 15px;
}
.picitem{
  position: relative;
  margin-bottom: 16px;
  p{
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
    min-height: 30px;
    line-height: 1.2;
    font-size: 13px;
  }
  i{
    position: absolute;
    top: 2px;
    right: 5px;
    color: white;
    z-index: 3;
    padding-left: 13px;
    font-size: 12px;
    background-position: 0;
    background-repeat: no-repeat;
    background-size: 11px 10px;
    text-shadow: 1px 0 0 rgb(0, 0, 0);
    font-style: normal;
    background-image: url(data:image/svg+xml;base64,);
  }
}
</style>